@import "../../styles/responsive/breakpoints.scss";

.project-cards-single-card {
	display: flex;
	flex-direction: column;
	margin-top: 25px;
	margin-bottom: 25px;
	height: 550px;
	width: 350px;
	background-color: white;
	box-shadow: 5px 5px 25px 3px black;
	transition: all 0.8s ease;
	position: relative;

	&:hover {
		transform: rotateX(15deg) rotateZ(5deg) rotateY(-10deg);
		box-shadow: 20px 15px 30px 5px rgba(78, 59, 77, 0.617);

		@include screens {
			transform: none;
		}
		@include screenm {
			transform: none;
		}
		@include screenl {
			transform: none;
		}
	}

	.project-cards-image {
		height: 200px;
		width: 100%;
		position: relative;
		object-fit: cover;
		cursor: pointer;
		transition: opacity 0.8s ease-in-out;

		&:hover {
			opacity: 80%;
		}
	}

	.project-cards-description {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		color: black;
		width: 100%;

		.project-cards-description-title {
			font-size: 18px;
			margin-top: 0rem;

			@include screens {
				margin-top: 0rem;
				font-size: 20px;
			}
		}

		.project-cards-description-paragraph {
			width: 85%;
			text-align: justify;
		}
	}

	.project-cards-tags-row {
		display: flex;
		justify-content: center;
		width: 100%;

		@include screens {
			height: 10%;
		}

		.project-card-single-tag {
			cursor: default;
			min-width: 100px;
			width: auto;
			height: 30px;
			background-color: rgba(162, 162, 162, 0.764);
			color: black;
			margin: 0 auto;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 500px;
		}
	}

	.project-cards-buttons {
		position: absolute;
		bottom: 0%;
		margin-top: 15px;
		display: flex;
		width: 100%;
		justify-content: space-around;
		align-items: center;
		height: 15%;
		background-color: black;

		@include screens {
			height: 15%;
		}

		.project-cards-single-button {
			cursor: pointer;
			display: flex;
			justify-content: center;
			align-items: center;
			color: white;
			height: 35px;
			width: 100px;
			background: none;
			border: 1px solid white;
			transition: all 0.3s ease-in;
			text-decoration: none;

			&:hover {
				background-color: white;
				color: black;
			}
		}
	}
}
